.nav {
  background-color: rgba(11, 13, 17, 0.479); }
  .nav .navbar-form {
    margin-right: 0px; }

.navbar-overlay {
  background-image: url("../imgs/bg-well.jpg"); }

#dashboard input.search {
  border: 0 !important;
  border-bottom: 3px solid #294399 !important;
  border-radius: 0px !important;
  box-shadow: 0px !important; }

#dashboard ul > li {
  margin-top: 3px; }
  #dashboard ul > li > a:hover {
    background-color: inherit;
    border-radius: 0px;
    height: 60px;
    border-bottom: 7px solid white; }

#dashboard span.fa,
#dashboard strong {
  color: white; }

#dashboard .fa-search:hover {
  font-weight: bolder; }

#dashboard i.fa-plus-circle {
  float: right;
  background-color: white;
  border-radius: 50%; }

@media (min-width: 992px) {
  #dashboard .nav-tabs li {
    margin-left: 3em;
    margin-right: 1.5em; } }

span > .badge {
  margin-bottom: 20px;
  margin-left: -12px;
  background-color: #a11f1f; }
